        background-image: url(/images/sky-small.webp)
    }
} */

.container {
    padding: 20px 5px 50px 20px;
    display: flex;
    flex-direction: column;
    min-height: 90vh;
    justify-content: space-between;
}
.App {
    display: flex;
    flex-wrap: wrap;
}

.search {
    position: absolute; right:0;
}

.info-block {
    display: flex;
    align-items: center;
    gap: 50px;
    flex-wrap: wrap;
}

.blockSearch {
    display: flex;
    margin-top: 100px ;
}


.temperature {
    font-size: 62px;
}

.time {
    max-width: 350px;
}

.place-time, .description {
    display: flex;
    flex-direction: column;
}

.date {
    margin-left: 5px;
}
.value {
    font-size: 25px;
}

.name {
    font-size: 25px;
}

.errorText {
    color: red;
    font-size: 25px;
    padding-left: 30px;
}

.main {
    font-size: 25px;
}

.error {
    border: 1px solid red;
    display: block;
    max-width: 300px;
}


.search-button {
    background-color: transparent;
    padding: 5px 10px;
    border: .5px solid #c4c4;
}




input {
    background: transparent;
    border: none;
    font-size: 28px;
    margin: 20px;
    outline:none;
    padding-left: 10px;
}

.search-button:hover {
    background-color: #c4c4;
}

input:hover {
    border-left: 3px solid #c4c4;

}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: rgb(42, 30, 30);
    opacity: 1; /* Firefox */
  }


  ::-ms-input-placeholder { /* Microsoft Edge */
    color: rgb(42, 30, 30);
  }

.details {
    border-top: .5px solid #c4c4;

}

h3 {
    margin: 30px 0;
}

.weather-block div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 20px;
    margin-bottom: 15px;
    gap: 150px;
}

@media (max-width: 1000px) {
    .App {
        justify-content: center;
    }
    .search {
        position: static;
    }
    .container {
        display: flex;
        flex-direction: column;
        min-height: 90vh;
    }

    .blockSearch {
        margin: 50px 0 50px 0;
    }
}



@media (max-width: 540px) {

    .search {
        position: static;
    }
    .container {
        display: flex;
        flex-direction: column;
        min-height: 90vh;
    }

    .blockSearch {
        margin: 50px 0 50px 0;
    }
    .search {
        max-width: 300px;
    }
    .blockSearch {
        max-width: 300px;
    }
   .form {
       max-width: 250px;
   }
    .weather-block div {
        gap: 0;
    }
}
